.featured{
  height:100vh;
  transition: all ease .2s;
}
.featured--vertical{
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured--horizontal{
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
}
.featured--name{
  font-size: 60px;
  font-weight: bold;
}
.featured--info{
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}
.featured--points,
.featured--year,
.featured--seasons{
  display: inline-block;
  margin-right: 15px;
}
.featured--points{
  color: #46d369;
}
.featured--description{
  margin-top: 15px;
  max-width: 40%;
  color: #999;
  font-size: 20px;
}
.featured--buttons{
  margin-top: 15px;
}
.featured--watchbutton,
.featured--mylistbutton{
  display: inline-block;
  font-weight: bold;
  font-size: 20px;
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 10px;
  opacity:1;
  transition: all ease .2s;
}
.featured--watchbutton{
  background-color: #fff;
  color: #000000;
}
.featured--mylistbutton{
  background-color: #333;
  color: #FFF;
}
.featured--watchbutton:hover,
.featured--mylistbutton:hover{
  opacity:.7;
}
.featured--genres{
  font-style: 18px;
  margin-top: 15px;
  color: #999;
}

@media (max-width:760px){
  .featured{
    height: 90vh;
  }
  .featured--name{
    font-size: 40px;
  }
  .featured--info{
    font-size: 16px;
  }
  .featured--description{
    font-size: 14px;
    max-width: 100%;
    margin-right: 30px;
  }
  .featured--watchbutton,
  .featured--mylistbutton{
    font-size: 16px;
  }
  .featured--genres{
    font-size: 14px;
  }
}