@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.movieRow{
  margin-bottom: 30px;
}
.movieRow h2{
  margin: 0 0 0 30px;
}
.movieRow--listarea{
  overflow-x: hidden;
  padding-left: 30px;
}
.movieRow--list{
  transition: all ease .5s;
}
.movieRow--item{
  display: inline-block;
  width:150px;
}
.movieRow--item img{
  width:100%;
  transform:scale(.9);
  transition: all ease .3s;
  cursor: pointer;
}
.movieRow--item img:hover{
  transform:scale(1);
}
.movieRow--left,
.movieRow--right{
  position: absolute;
  width: 40px;
  height: 225px;
  background-color: rgba(0,0,0,.6);
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
  opacity: 0;
  transition: all ease .5s;
}
.movieRow--left{
  left: 0;
}
.movieRow--right{
  right: 0;
}
.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--right{
  opacity: 1;
}

@media (max-width:760px){
  .movieRow--left,
  .movieRow--right{
    opacity: 1;
  }
}
.featured{
  height:100vh;
  transition: all ease .2s;
}
.featured--vertical{
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured--horizontal{
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
}
.featured--name{
  font-size: 60px;
  font-weight: bold;
}
.featured--info{
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}
.featured--points,
.featured--year,
.featured--seasons{
  display: inline-block;
  margin-right: 15px;
}
.featured--points{
  color: #46d369;
}
.featured--description{
  margin-top: 15px;
  max-width: 40%;
  color: #999;
  font-size: 20px;
}
.featured--buttons{
  margin-top: 15px;
}
.featured--watchbutton,
.featured--mylistbutton{
  display: inline-block;
  font-weight: bold;
  font-size: 20px;
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 10px;
  opacity:1;
  transition: all ease .2s;
}
.featured--watchbutton{
  background-color: #fff;
  color: #000000;
}
.featured--mylistbutton{
  background-color: #333;
  color: #FFF;
}
.featured--watchbutton:hover,
.featured--mylistbutton:hover{
  opacity:.7;
}
.featured--genres{
  font-style: 18px;
  margin-top: 15px;
  color: #999;
}

@media (max-width:760px){
  .featured{
    height: 90vh;
  }
  .featured--name{
    font-size: 40px;
  }
  .featured--info{
    font-size: 16px;
  }
  .featured--description{
    font-size: 14px;
    max-width: 100%;
    margin-right: 30px;
  }
  .featured--watchbutton,
  .featured--mylistbutton{
    font-size: 16px;
  }
  .featured--genres{
    font-size: 14px;
  }
}
header{
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  background-color:transparent;
  transition: all ease .5s;
}
header.black{
  background-color: #141414;
}
.header--logo{
  height: 25px;
}
.header--logo img{
  height: 100%;
}
.header--user{
  height: 35px;
}
.header--user img{
  height: 100%;
  border-radius: 3px;
}
* {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-sizing: border-box;
}
body{
  background-color:#111;
  font-family: 'Roboto', sans-serif;
  margin:0;
  color: #FFF;
}
.lists{
  margin-top: -150px;
}
footer{
  margin: 50px 0;
  text-align: center;
  font-size: 12px;
  color: #999;
}
.loading{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 99;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.loading img{
  width: 500px;
}

@media (max-width:760px){
  footer{
    width: 70%;
    margin: 0 auto;
  }
}
